import React, {Component} from 'react';
import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';

export default function BasicSelect() {
    const [age, setAge] = React.useState('');
    const [detail, setDetail] = React.useState('');
    const [detailThree, setDetailThree] = React.useState('');
    const [detailFour, setDetailFour] = React.useState('');
  
    const handleChange = (event) => {
        setAge(event.target.value);
    };
    const detailChange = (event) => {
        setDetail(event.target.value);
    };
    const detailThreeChange = (event) => {
        setDetailThree(event.target.value);
    };
    const detailFourChange = (event) => {
        setDetailFour(event.target.value);
    };

    return (
        <Box sx={{ mx: "auto" }}>
            <FormControl fullWidth>
                <InputLabel id="depth-one">1차 분류</InputLabel>
                <Select
                labelId="depth-one"
                id="depth-one-select"
                value={age}
                label="1차 분류"
                onChange={handleChange}
                >
                    <MenuItem value={10}>패션</MenuItem>
                    <MenuItem value={20}>전자기기</MenuItem>
                    <MenuItem value={30}>가구</MenuItem>
                    <MenuItem value={40}>스포츠</MenuItem>
                    <MenuItem value={50}>식품</MenuItem>
                </Select>
            </FormControl>

            <FormControl fullWidth>
                <InputLabel id="depth-two">2차 분류</InputLabel>
                <Select
                labelId="depth-two"
                id="depth-two-select"
                value={detail}
                label="2차 분류"
                onChange={detailChange}
                >
                    <MenuItem value={10}>상세1</MenuItem>
                    <MenuItem value={20}>상세2</MenuItem>
                    <MenuItem value={30}>상세3</MenuItem>
                    <MenuItem value={40}>상세4</MenuItem>
                    <MenuItem value={50}>상세5</MenuItem>
                </Select>
            </FormControl>

            <FormControl fullWidth>
                <InputLabel id="depth-three">3차 분류</InputLabel>
                <Select
                labelId="depth-three"
                id="depth-three-select"
                value={detailThree}
                label="3차 분류"
                onChange={detailThreeChange}
                >
                    <MenuItem value={10}>3차 상세 1</MenuItem>
                    <MenuItem value={20}>3차 상세 2</MenuItem>
                    <MenuItem value={30}>3차 상세 3</MenuItem>
                    <MenuItem value={40}>3차 상세 4</MenuItem>
                    <MenuItem value={50}>3차 상세 5</MenuItem>
                </Select>
            </FormControl>

            <FormControl fullWidth>
                <InputLabel id="depth-four">4차 분류</InputLabel>
                <Select
                labelId="depth-four"
                id="depth-four-select"
                value={detailFour}
                label="4차 분류"
                onChange={detailFourChange}
                >
                    <MenuItem value={10}>4차 상세 1</MenuItem>
                    <MenuItem value={20}>4차 상세 2</MenuItem>
                    <MenuItem value={30}>4차 상세 3</MenuItem>
                    <MenuItem value={40}>4차 상세 4</MenuItem>
                    <MenuItem value={50}>4차 상세 5</MenuItem>
                </Select>
            </FormControl>
        </Box>
    );
}